import React, { useEffect, useState } from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

const ActivationFunctionsComparison = () => {
  const [data, setData] = useState([]);
  const alpha = 0.01; // Leaky ReLUのα値
  
  useEffect(() => {
    const generateData = () => {
      const dataPoints = [];
      for (let x = -10; x <= 10; x += 0.5) {
        const sigmoid = 1 / (1 + Math.exp(-x));
        const tanh = Math.tanh(x);
        const relu = Math.max(0, x);
        const leakyRelu = x > 0 ? x : alpha * x;
        dataPoints.push({ 
          x, 
          sigmoid, 
          tanh, 
          relu, 
          leakyRelu 
        });
      }
      return dataPoints;
    };
    
    setData(generateData());
  }, [alpha]);
  
  return (
    <div className="flex flex-col items-center w-full visualization-section">
      <h3 className="text-xl font-bold mb-4">活性化関数の比較</h3>
      <div className="w-full h-80 bg-white p-4 rounded-lg shadow-md">
        <ResponsiveContainer width="100%" height="100%">
          <LineChart data={data} margin={{ top: 5, right: 30, left: 20, bottom: 5 }}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis 
              dataKey="x" 
              label={{ value: 'x', position: 'insideBottomRight', offset: -5 }} 
              domain={[-6, 6]}
            />
            <YAxis 
              label={{ value: 'f(x)', angle: -90, position: 'insideLeft' }} 
              domain={[-1.5, 6]}
            />
            <Tooltip 
              formatter={(value) => [value.toFixed(4), '']}
              labelFormatter={(value) => `x: ${value}`}
            />
            <Legend />
            <Line 
              type="monotone" 
              dataKey="sigmoid" 
              stroke="#8884d8" 
              strokeWidth={2} 
              dot={false} 
              name="シグモイド"
            />
            <Line 
              type="monotone" 
              dataKey="tanh" 
              stroke="#82ca9d" 
              strokeWidth={2} 
              dot={false} 
              name="tanh"
            />
            <Line 
              type="monotone" 
              dataKey="relu" 
              stroke="#ff7300" 
              strokeWidth={2} 
              dot={false} 
              name="ReLU"
            />
            <Line 
              type="monotone" 
              dataKey="leakyRelu" 
              stroke="#8844d8" 
              strokeWidth={2} 
              dot={false} 
              name="Leaky ReLU"
            />
          </LineChart>
        </ResponsiveContainer>
      </div>
      
      <div className="mt-6 w-full p-4 bg-gray-50 rounded-lg">
        <h4 className="font-bold mb-3">G検定で重要な活性化関数の比較ポイント</h4>
        <div className="overflow-x-auto">
          <table className="min-w-full bg-white">
            <thead>
              <tr>
                <th className="p-2 border">活性化関数</th>
                <th className="p-2 border">出力範囲</th>
                <th className="p-2 border">微分可能性</th>
                <th className="p-2 border">長所</th>
                <th className="p-2 border">短所</th>
                <th className="p-2 border">主な用途</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td className="p-2 border font-bold">シグモイド</td>
                <td className="p-2 border">0〜1</td>
                <td className="p-2 border">すべての点で微分可能</td>
                <td className="p-2 border">出力を確率として解釈可能</td>
                <td className="p-2 border">勾配消失問題<br/>計算コストが高い</td>
                <td className="p-2 border">二値分類の出力層</td>
              </tr>
              <tr>
                <td className="p-2 border font-bold">tanh</td>
                <td className="p-2 border">-1〜1</td>
                <td className="p-2 border">すべての点で微分可能</td>
                <td className="p-2 border">原点対称<br/>シグモイドより勾配消失が少ない</td>
                <td className="p-2 border">依然として勾配消失問題あり</td>
                <td className="p-2 border">RNNの隠れ層<br/>入力が正負両方の場合</td>
              </tr>
              <tr>
                <td className="p-2 border font-bold">ReLU</td>
                <td className="p-2 border">0〜∞</td>
                <td className="p-2 border">x=0で微分不可能</td>
                <td className="p-2 border">計算が単純・高速<br/>勾配消失問題を軽減<br/>スパース活性化</td>
                <td className="p-2 border">Dying ReLU問題<br/>(負の入力で学習停止)</td>
                <td className="p-2 border">CNNの隠れ層<br/>ディープネットワーク</td>
              </tr>
              <tr>
                <td className="p-2 border font-bold">Leaky ReLU</td>
                <td className="p-2 border">-∞〜∞</td>
                <td className="p-2 border">x=0で微分不可能</td>
                <td className="p-2 border">Dying ReLU問題を解決<br/>ReLUの利点を保持</td>
                <td className="p-2 border">αの値を調整する必要あり<br/>ReLUより複雑</td>
                <td className="p-2 border">ReLUで問題が発生する場合</td>
              </tr>
              <tr>
                <td className="p-2 border font-bold">Softmax</td>
                <td className="p-2 border">0〜1<br/>(合計=1)</td>
                <td className="p-2 border">すべての点で微分可能</td>
                <td className="p-2 border">多クラス確率分布<br/>出力の合計が1</td>
                <td className="p-2 border">多入力・多出力のため<br/>計算コストが高い</td>
                <td className="p-2 border">多クラス分類の出力層</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      
      <div className="mt-4 p-4 bg-yellow-50 rounded-lg w-full">
        <h4 className="font-bold mb-2">G検定のための重要ポイント</h4>
        <ul className="list-disc pl-5">
          <li>勾配消失問題とは、深いネットワークで勾配が0に近づき、学習が進まなくなる問題</li>
          <li>ReLUファミリー(ReLU, Leaky ReLU)は現代のディープラーニングで最も広く使われている</li>
          <li>出力層ではタスクに応じた活性化関数を選ぶ（二値分類ではシグモイド、多クラス分類ではSoftmax）</li>
          <li>隠れ層ではReLUファミリーが一般的（計算効率とスパース性）</li>
          <li>活性化関数がないと、ニューラルネットワークは線形変換の重ね合わせとなり、複雑な関数を表現できない</li>
        </ul>
      </div>
    </div>
  );
};

export default ActivationFunctionsComparison;