import React from 'react';
import './index.css';

// 活性化関数コンポーネントをインポート
import SigmoidFunction from './components/activation-functions/SigmoidFunction';
import TanhFunction from './components/activation-functions/TanhFunction';
import ReLUFunction from './components/activation-functions/ReLUFunction';
import LeakyReLUFunction from './components/activation-functions/LeakyReLUFunction';
import SoftmaxVisualizer from './components/activation-functions/SoftmaxVisualizer';
import ActivationFunctionsComparison from './components/activation-functions/ActivationFunctionsComparison';

// 評価指標コンポーネントをインポート
import ConfusionMatrix from './components/evaluation-metrics/ConfusionMatrix';
import MetricsVisualizer from './components/evaluation-metrics/MetricsVisualizer';
import MetricsComparison from './components/evaluation-metrics/MetricsComparison';

function App() {
  return (
    <div className="container mx-auto p-4">
      <header className="app-header">
        <h1 className="text-3xl font-bold">G検定のための視覚化ツール</h1>
        <p className="mt-2">活性化関数と評価指標を理解しよう</p>
      </header>

      <main>
        {/* 活性化関数セクション */}
        <section className="mb-8">
          <h2 className="text-2xl font-bold mb-4 p-2 bg-blue-100 rounded">活性化関数</h2>
          
          <ActivationFunctionsComparison />
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mt-6">
            <SigmoidFunction />
            <TanhFunction />
            <ReLUFunction />
            <LeakyReLUFunction />
          </div>
          
          <div className="mt-6">
            <SoftmaxVisualizer />
          </div>
        </section>
        
        {/* 評価指標セクション */}
        <section>
          <h2 className="text-2xl font-bold mb-4 p-2 bg-green-100 rounded">評価指標</h2>
          
          <ConfusionMatrix />
          
          <div className="mt-6">
            <MetricsVisualizer />
          </div>
          
          <div className="mt-6">
            <MetricsComparison />
          </div>
        </section>
      </main>
      
      <footer className="mt-12 pt-6 border-t text-center text-gray-500">
        <p>G検定学習用ビジュアライゼーションツール</p>
        <p className="mt-2">© {new Date().getFullYear()} Deep Learning学習支援ツール</p>
      </footer>
    </div>
  );
}

export default App;