import React, { useEffect, useState } from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

const TanhFunction = () => {
  const [data, setData] = useState([]);
  
  useEffect(() => {
    const generateData = () => {
      const dataPoints = [];
      for (let x = -10; x <= 10; x += 0.5) {
        const tanh = Math.tanh(x);
        dataPoints.push({ x, y: tanh });
      }
      return dataPoints;
    };
    
    setData(generateData());
  }, []);
  
  return (
    <div className="flex flex-col items-center w-full visualization-section">
      <h3 className="text-xl font-bold mb-4">tanh関数: f(x) = (e^x - e^(-x))/(e^x + e^(-x))</h3>
      <div className="w-full h-64 bg-white p-4 rounded-lg shadow-md">
        <ResponsiveContainer width="100%" height="100%">
          <LineChart data={data} margin={{ top: 5, right: 30, left: 20, bottom: 5 }}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis 
              dataKey="x" 
              label={{ value: 'x', position: 'insideBottomRight', offset: -5 }} 
              domain={[-10, 10]}
            />
            <YAxis 
              label={{ value: 'f(x)', angle: -90, position: 'insideLeft' }} 
              domain={[-1.1, 1.1]}
            />
            <Tooltip 
              formatter={(value) => [value.toFixed(4), 'f(x)']}
              labelFormatter={(value) => `x: ${value}`}
            />
            <Line 
              type="monotone" 
              dataKey="y" 
              stroke="#82ca9d" 
              strokeWidth={2} 
              dot={false} 
              name="tanh"
            />
            {/* 重要なポイントをマーク */}
            <Line 
              dataKey="y" 
              stroke="transparent" 
              dot={(props) => {
                const { cx, cy, payload } = props;
                if (payload.x === 0) {
                  return (
                    <circle 
                      cx={cx} 
                      cy={cy} 
                      r={4} 
                      stroke="red" 
                      strokeWidth={2} 
                      fill="red" 
                    />
                  );
                }
                return null;
              }}
            />
          </LineChart>
        </ResponsiveContainer>
      </div>
      <div className="mt-4 p-4 bg-green-50 rounded-lg">
        <h4 className="font-bold mb-2">ポイント</h4>
        <ul className="list-disc pl-5">
          <li>x=0のとき、f(x)=0 (グラフの赤い点)</li>
          <li>原点対称の形状</li>
          <li>xが正の大きな値では1に、負の大きな値では-1に漸近</li>
          <li>シグモイド関数より勾配が大きく、学習がやや速い</li>
          <li>出力範囲: -1〜1</li>
          <li>RNNの隠れ層などでよく使用される</li>
        </ul>
      </div>
    </div>
  );
};

export default TanhFunction;