import React from 'react';

const MetricsComparison = () => {
  return (
    <div className="flex flex-col items-center w-full visualization-section">
      <h3 className="text-xl font-bold mb-4">評価指標の比較とユースケース</h3>
      
      <div className="w-full p-4 bg-white rounded-lg shadow-md mb-6">
        <p className="mb-4">各評価指標は異なる状況で重要性が変わります。以下はシナリオ別の最適な評価指標です：</p>
        
        <div className="overflow-x-auto">
          <table className="min-w-full bg-white">
            <thead>
              <tr>
                <th className="p-2 border">シナリオ</th>
                <th className="p-2 border">重視すべき指標</th>
                <th className="p-2 border">理由</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td className="p-2 border">医療診断<br/>(病気の検出)</td>
                <td className="p-2 border font-bold text-purple-700">Recall (再現率)</td>
                <td className="p-2 border">病気を見逃す（偽陰性）ことは深刻な結果につながるため、実際の病気をできるだけ多く検出することが重要</td>
              </tr>
              <tr>
                <td className="p-2 border">スパムメールフィルター</td>
                <td className="p-2 border font-bold text-green-700">Precision (適合率)</td>
                <td className="p-2 border">重要なメールを誤ってスパム（偽陽性）と判定すると問題が生じるため、スパムと判定したメールが確実にスパムであることが重要</td>
              </tr>
              <tr>
                <td className="p-2 border">犯罪予測システム</td>
                <td className="p-2 border font-bold text-red-700">F1 Score</td>
                <td className="p-2 border">犯罪を見逃さないこと（高Recall）と、無実の人を疑わないこと（高Precision）の両方が重要</td>
              </tr>
              <tr>
                <td className="p-2 border">バランスのとれたデータセット<br/>での一般的な分類</td>
                <td className="p-2 border font-bold text-blue-700">Accuracy (正解率)</td>
                <td className="p-2 border">クラスが均等に分布している場合、全体的な正解率が性能の良い指標となる</td>
              </tr>
              <tr>
                <td className="p-2 border">不均衡データセット<br/>(例: 詐欺検出)</td>
                <td className="p-2 border font-bold text-red-700">F1 Score</td>
                <td className="p-2 border">少数クラス（詐欺取引）の検出性能を評価するために、PrecisionとRecallのバランスを見る必要がある</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      
      {/* 視覚的な違いの説明 */}
      <div className="w-full bg-gray-50 p-4 rounded-lg mb-6">
        <h4 className="font-bold mb-3">各指標が重視するケース</h4>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div className="bg-white p-3 rounded-lg shadow">
            <h5 className="font-bold text-green-700 mb-2">Precision が高い場合</h5>
            <div className="flex justify-center mb-3">
              <svg width="180" height="120" viewBox="0 0 180 120">
                <circle cx="60" cy="60" r="40" fill="#EFF6FF" stroke="#3B82F6" strokeWidth="2" />
                <circle cx="120" cy="60" r="40" fill="#FEF2F2" stroke="#EF4444" strokeWidth="2" />
                <circle cx="75" cy="60" r="25" fill="#93C5FD" opacity="0.8" />
                <text x="45" y="100" fontSize="12" textAnchor="middle">実際の陽性</text>
                <text x="135" y="100" fontSize="12" textAnchor="middle">実際の陰性</text>
                <text x="75" y="20" fontSize="12" textAnchor="middle">予測:陽性</text>
              </svg>
            </div>
            <p className="text-sm">
              <strong>特徴:</strong> 陽性と予測したもののほとんどが実際に陽性（予測の確実性が高い）
            </p>
            <p className="text-sm mt-1">
              <strong>例:</strong> メールをスパムと分類した場合、それが本当にスパムである確率が高い
            </p>
          </div>
          
          <div className="bg-white p-3 rounded-lg shadow">
            <h5 className="font-bold text-purple-700 mb-2">Recall が高い場合</h5>
            <div className="flex justify-center mb-3">
              <svg width="180" height="120" viewBox="0 0 180 120">
                <circle cx="60" cy="60" r="40" fill="#EFF6FF" stroke="#3B82F6" strokeWidth="2" />
                <circle cx="120" cy="60" r="40" fill="#FEF2F2" stroke="#EF4444" strokeWidth="2" />
                <ellipse cx="60" cy="60" rx="35" ry="30" fill="#93C5FD" opacity="0.8" />
                <text x="45" y="100" fontSize="12" textAnchor="middle">実際の陽性</text>
                <text x="135" y="100" fontSize="12" textAnchor="middle">実際の陰性</text>
                <text x="75" y="20" fontSize="12" textAnchor="middle">予測:陽性</text>
              </svg>
            </div>
            <p className="text-sm">
              <strong>特徴:</strong> 実際の陽性例のほとんどを正しく検出（検出の網羅性が高い）
            </p>
            <p className="text-sm mt-1">
              <strong>例:</strong> 病気の患者をほぼ全員検出できるが、健康な人も病気と判定してしまう可能性
            </p>
          </div>
        </div>
      </div>
      
      {/* Precision-Recallのトレードオフ */}
      <div className="w-full bg-white p-4 rounded-lg shadow-md">
        <h4 className="font-bold mb-3 text-center">Precision-Recallのトレードオフ</h4>
        
        <div className="flex justify-center mb-4">
          <svg width="400" height="220" viewBox="0 0 400 220">
            {/* 座標軸 */}
            <line x1="50" y1="170" x2="350" y2="170" stroke="black" strokeWidth="2" />
            <line x1="50" y1="170" x2="50" y2="20" stroke="black" strokeWidth="2" />
            
            {/* 目盛り */}
            <text x="380" y="175" fontSize="12">閾値</text>
            <text x="30" y="10" fontSize="12">値</text>
            
            {/* カーブ */}
            <path d="M50,170 C100,150 130,100 170,80 C210,60 240,50 270,45 C300,40 320,38 350,35" 
                  stroke="#047857" strokeWidth="2" fill="none" />
            <path d="M50,35 C80,40 110,45 140,60 C170,75 200,110 230,130 C260,150 300,160 350,170" 
                  stroke="#7E22CE" strokeWidth="2" fill="none" />
            
            {/* ラベル */}
            <text x="320" y="40" fontSize="12" fill="#047857" fontWeight="bold">Precision</text>
            <text x="320" y="150" fontSize="12" fill="#7E22CE" fontWeight="bold">Recall</text>
            
            {/* 矢印と閾値ポイント */}
            <line x1="200" y1="185" x2="200" y2="170" stroke="red" strokeWidth="2" />
            <text x="200" y="200" fontSize="10" textAnchor="middle">閾値</text>
            
            {/* F1スコアが最大になる点 */}
            <circle cx="200" cy="95" r="5" fill="red" />
            <text x="200" y="85" fontSize="10" textAnchor="middle">F1スコア最大</text>
          </svg>
        </div>
        
        <div className="text-sm p-3 bg-yellow-50 rounded">
          <p><strong>PrecisionとRecallのトレードオフ:</strong></p>
          <ul className="list-disc pl-5 mt-2">
            <li>閾値を上げると（モデルがより確信を持つ場合のみ陽性と予測）: Precisionが上がり、Recallが下がる</li>
            <li>閾値を下げると（より多くのケースを陽性と予測）: Recallが上がり、Precisionが下がる</li>
            <li>F1スコアは、PrecisionとRecallのバランスが取れた点で最大になる</li>
            <li><strong>G検定ポイント:</strong> タスクの性質に応じて適切な評価指標と閾値を選ぶことが重要</li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default MetricsComparison;